<script scoped>
  export default {
    name: 'AboutMe',
    mounted: function() {
      window.addEventListener('scroll', this.drawOnScroll)
      this.drawOnScroll();
    },
    methods: {
      drawOnScroll(){
        var aosvg = document.querySelector('.title-stroke');
        var aosvgunder = document.querySelector('.title-yellow');

        var initial = 400;

        const style = getComputedStyle(aosvg).strokeDashoffset;


        const new_unit = parseInt(style, 10);
        var offset_unit = window.innerHeight/initial;

        var desc = initial/window.innerHeight;
        var new_style = initial - (document.documentElement.scrollTop/(window.innerHeight/initial));

        aosvg.style.strokeDashoffset = new_style;

        if (new_style > -30 && new_style < 90) {
          aosvg.style.strokeDashoffset = 0;
          aosvgunder.style.marginLeft = "3px";
          aosvgunder.style.marginTop = "3px";
          aosvgunder.style.opacity = "1";
        } else {
          aosvg.style.fillOpacity = 0;
          aosvg.style.strokeDashoffset = new_style;
          aosvgunder.style.opacity = "0";
          aosvgunder.style.marginLeft = "0px";
          aosvgunder.style.marginTop = "0px";
        }
      },
    }
  }
