import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Working from '../views/Working.vue'
import Admin from '../views/Admin.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'working',
    component: Working
  },
  {
    path: '/working',
    name: 'working',
    component: Working
  },
  {
    path: '/admin',
    name: 'admin',
    component: Admin
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router
