<template>
  <div v-scroll-reveal.reset>
    <div v-swiper:mySwiper="swiperOption">
      <div class="swiper-wrapper timeline">
        <div class="swiper-slide">
          <div class="timestamp">
            <span class="date"> </span>
          </div>
          <div class="status-start">
          </div>
        </div>
        <div class="swiper-slide" v-for="(item, index) in steps" v-bind:id="item.id" v-on:click="openModal($event)">
          <div class="timestamp">
            <span class="date">{{item.dateLabel}}</span>
          </div>
          <div class="status">
            <span>{{item.title}}</span>
          </div>

          <b-modal :id="item.id" size="lg" centered scrollable hide-footer hide-header body-class="modal-body" content-class="modal-content">
            <div class="modal-iterate">
              <b-btn class="float-l" v-if="index != 0" v-on:click="changeModal(item.id, 'prev')"><</b-btn>
              <b-btn class="float-r" v-if="index != steps.length - 1" v-on:click="changeModal(item.id, 'next')">></b-btn>
            </div>
            
            <div style="text-align: center;">
              <h3> {{item.title}} </h3>
            </div>
            <p class="my-4"> {{item.description}} </p>
          </b-modal>
        </div>
        <div class="swiper-slide">
          <div class="timestamp">
            <span class="date"> </span>
          </div>
          <div class="status-end">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script scoped>
  import { Swiper, SwiperSlide, directive } from 'vue-awesome-swiper'
  import 'swiper/swiper-bundle.css'

  const data = [
    { id: '0', dateLabel: 'September 2015', title: 'Started studying at University of Minho', description: 'In 2015 i started... ' },
    { id: '1', dateLabel: 'July 2016', title: 'Summer internship at Continental', description: 'In 2016 i started... ' },
    { id: '2', dateLabel: 'September 2019', title: 'Internship at Bsolus', description: 'In 2019 i started... ' },
    { id: '3', dateLabel: 'September 2020', title: 'Started working at Bsolus', description: 'In 2020 i started... ' },
    { id: '4', dateLabel: 'February 2021', title: 'Graduated with a Master\'s Degree in Software Engineering', description: 'In 2021 i concluded my masters degree in Software Engineering where I presented my thesis...' }
  ];

  export default {
    name: 'Timeline',
    components: {
      Swiper,
      SwiperSlide
    },
    mounted: function() {
      window.addEventListener('scroll', this.drawOnScroll)
      this.drawOnScroll();
    },
    data: function () {
      return {
        steps: data,
        swiperOption: {
          slidesPerView: 4,
          initialSlide: 4,
          grabCursor: true,
          shortSwipes: false,
          longSwipesMs: 0.01
        }
      }
    },
    methods: {
      openModal: function(event) {
        var targetId = event.currentTarget.id;
        this.$bvModal.show(targetId);
      },
      changeModal: function(currModalId, event) {
        var currModal = Number(currModalId);
        if (event == "prev" || event == "next") {
          if (event == "prev") {
            var nextModalId = (currModal - 1).toString()
          } else if (event == "next") {
            var nextModalId = (currModal + 1).toString()
          }
          var idArray = data.map(function (el) { return el.id; });

          if (idArray.includes(nextModalId)) {
            this.$bvModal.show(nextModalId);
            this.$bvModal.hide(currModalId);
          }
        }
      },
      drawOnScroll(){
        var aosvg = document.querySelector('.title-stroke');
        var aosvgunder = document.querySelector('.title-yellow');

        var initial = 400;

        const style = getComputedStyle(aosvg).strokeDashoffset;

        const new_unit = parseInt(style, 10);
        var offset_unit = window.innerHeight/initial;

        var desc = initial/window.innerHeight;
        var new_style = initial - (document.documentElement.scrollTop/(window.innerHeight/initial));

        aosvg.style.strokeDashoffset = new_style;

        if (new_style > -30 && new_style < 90) {
          aosvg.style.strokeDashoffset = 0;
          aosvgunder.style.marginLeft = "3px";
          aosvgunder.style.marginTop = "3px";
          aosvgunder.style.opacity = "1";
        } else {
          aosvg.style.fillOpacity = 0;
          aosvg.style.strokeDashoffset = new_style;
          aosvgunder.style.opacity = "0";
          aosvgunder.style.marginLeft = "0px";
          aosvgunder.style.marginTop = "0px";
        }
      },
    }
  }
</script>

<style scoped>
  @import '../assets/styles/main.css';
  @import '../assets/styles/layout.css';
  @import '../assets/styles/timeline.css';
</style>
