<script scoped>
  export default {
    name: 'Bar',
    data() {
      return {
        window: {
          width: 0,
          height: 0
        }
      }
    },
    created: function() {
      window.addEventListener('resize', this.handleResize)
      this.handleResize();
    },
    mounted: function() {
      window.addEventListener('scroll', this.handleScroll)
      this.handleScroll();
    },
    methods: {
      isMobile() {
        if(/Android|WebOS|iPhone|iPad|Blackberry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
          return true
        } else{
          return false
        }
      },
      profileClick() {
        //SUBSTITUIR PELO USER LOGADO!!!!
        this.$router.push('/vitorpeixoto');
      },
      inspireClick() {
        this.$router.push('/inspire');
      },
      handleResize() {
        this.window.width = window.innerWidth;
        this.window.height = window.innerHeight;
      },
      handleScroll(){
        var nav = this.$refs.deskbar;
        if(document.scrollingElement.scrollTop > 160){
          nav.classList.add('bar-dark');
          nav.classList.remove('bar-transp');
        } else {
          nav.classList.remove('bar-dark');
          nav.classList.add('bar-transp');
        }
      },
    }
  }
</script>

